.wrapper {
    width: 100%;
}

.content {
    box-sizing: border-box;
    max-width: @globalWidths[desktop];
    margin: 0px auto;
    padding: 40px 30px;

    @media (max-width: @globalWidths[mobile]) {
        padding: 24px;
    }

    h2 {
        font-size: 26px;
        margin-bottom: 40px;

        @media (max-width: @globalWidths[mobile]) {
            font-size: 24px;
            margin-bottom: 20px;
        }
    }
}

.projects {

}
@globalColors: {
    text: #333333;
    backgroundDefault: #ffffff;
    backgroundSlight: #e8eef2;
    conversion: #36a9dc;
}

@globalWidths: {
    desktop: 1140px;
    mobile: 768px;
}
